@use "../utils";
.nav-top {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center; }
  .nav-top__link {
    font-size: 12px;
    line-height: 16px;
    padding: 8px;
    position: relative; }
    @media (max-width: 900px) {
