@use "../utils";
@media (max-width: 900px) {
  .nav {
    display: none; } }

.nav__list {
  display: flex;
  justify-content: space-between; }

.nav__link {
  transition: color 0.3s;
  font-size: 12px;
  line-height: 16px;
  padding: 8px 24px; }
  .nav__link:hover {
    color: #9fadc2; }
  .nav__link--active {
    background: rgba(255, 255, 255, 0.1);
    border-radius: 8px; }

@use "../utils";
