.nav-toggle__list {
  display: flex;
  flex-direction: column; }

.nav-toggle__item {
  margin-bottom: 32px; }
  .nav-toggle__item--end {
    margin-bottom: 48px; }

.nav-toggle__item-call:last-child {
  margin-top: 16px; }

.nav-toggle__link {
  font-weight: bold;
  font-size: 22px;
  padding: 10px 0;
  position: relative; }
